@import config_colors

$__debug_flex_is_on: false

$font_family: $helvetica
$font_cousins: "KMBebasNeue", Sans-Serif

$light_color: $light_gray
$light_color_contrast: lighten($black, 10)

// Font Size
$base_font_size: 20px
$base_line_height_ratio: 1.6
$base_line_height: floor($base_font_size * $base_line_height_ratio)




//-----------------------------------*/
// General Sizing
//-----------------------------------*/
$padding: 30px
$halfling: 	golden-ratio($padding, -1)
$miniling: 	golden-ratio($padding, -2)
$bigling: 	golden-ratio($padding, 1)
$doubling: 	golden-ratio($padding, 2)

$max_width: 1080px




// -------------------------------------
// Transitions:
// -------------------------------------
$t_time: 400
$t_nice: cubic-bezier(.73,.14,.77,.77)
$t_function: $ease_in_out_quad
$t_function_in: $ease_in_quad
$t_function_out: $ease_out_quad


$border_radius: 6px
$header_animation_speed: 500 


//-----------------------------------*/
// Normalize
//-----------------------------------*/
$h1: (45 / 16) +0rem
$h2: (35 / 16) +0rem
$h3: (30 / 16) +0rem
$h4: (25 / 16) +0rem
$h5: (18 / 16) +0rem
$h6: (14 / 16) +0rem

$normalized_font_family: $font_family
$normalize_headings: false



//-----------------------------------*/
// Blankwork
//-----------------------------------*/
$desk_start: 1080px
$use_sass_placeholders: false
$blankwork_padding: $padding

$prefix-for-webkit:    true
$prefix-for-mozilla:   true
$prefix-for-microsoft: true
$prefix-for-opera:     false
$prefix-for-spec:      true

//-----------------------------------*/
// Misc
//-----------------------------------*/
$base_item_width: 1080px / 12
$headings: $h1 $h2 $h3 $h4 $h5 $h6 


//-----------------------------------*/
// Media
//-----------------------------------*/
$palm: max-width 767px    
$horizontal_palm: max-height 480px
$lap: min-width 768px
$portable: max-width $max_width -1 
