//-----------------------------------*/
// Transitions
//-----------------------------------*/
=t_in($what, $time: $t_time, $function: $t_function_in)
	transition: $what get_ms($time) $function

=t_out($what, $time: $t_time, $function: $t_function_out)
	transition: $what get_ms($time) $function

=t($what, $time: $t_time, $function: $t_function)
	transition: $what get_ms($time) $function


%transition__transform--quad
	transform:  translateY(100%) 
	transition-property: transform
	transition-duration: 350ms
	transition-timing-function: $ease_in_out_quad

%header_transition--expand
	+t(all, $header_animation_speed, $ease_in_quad)

%header_transition--contract
	+t(all, $header_animation_speed, $ease_out_back)