//-----------------------------------*/
// Nice Colors
//-----------------------------------*/

// Generic Colors:
$white: darken(white, 1)
$whiteish: darken($white, 3)
$black: lighten(black, 5)

$gray: #999999
$lighter_gray: darken(white, 2)
$light_gray: darken($lighter_gray, 4)


//-----------------------------------*/
// Background
//-----------------------------------*/
// Same as brand color, I just didn't want to reorganize
$background_color: #FFDD00 
$background_color_contrast: $black


$red: #de0405
$pink: #fe4365
$purple: #790f5b
$violet: #be80ff
$blue: #009BDD
$green: #7ab317
$yellow: #ffd700
$orange: #ff552c
$brown: #683829

// Flattish Colors
// $mint: #00bd82


//-----------------------------------*/
// Base Primary & Brand Colors
//-----------------------------------*/
$primary_color: #333333
$brand_color: #929611
// $brand_color: darken( saturate(#A3B286, 15), 18 )


//-----------------------------------*/
// Primary Color
//-----------------------------------*/
// Lighten    
$primary_color_light: lighten($primary_color, 5)
$primary_color_lighter: saturate(lighten($primary_color_light, 3), 8)

// Darken
$primary_color_dark: #0A0909
$primary_color_darker: saturate(darken($primary_color_dark, 3), 4)

$primary_color_contrast: #edefee

//-----------------------------------*/
// Brand Color
//-----------------------------------*/

// Lighten  	
$brand_color_light: #FFEA00
$brand_color_lighter: saturate(lighten($brand_color_light, 4), 8)

// Darken
$brand_color_dark: darken($brand_color, 6)
$brand_color_darker: desaturate(darken($brand_color_dark, 4), 4)



// Primary Contrast Color
$brand_color_contrast: $primary_color

// Links
$link_color: lighten($brand_color, 1)
$link_hover: darken($link_color, 10)

// Link on Accent
$link_on_brand: lighten( $primary_color, 18 )
$link_on_brand_hover: darken( $brand_color, 28 )

// Link on Primary
$link_on_primary: $white
$link_on_primary_hover: $brand_color_light

$light_color: $light_gray
$light_color_contrast: lighten($black, 10)


//-----------------------------------*/
// Transparent Colors
//-----------------------------------*/

$transparent_gray: transparentize( gray, 0.5 )

$transparent_white: transparentize($white, 0.8)
$transparent_black: transparentize($black, 0.8)

$transparent_medium_black: transparentize($black, 0.4)
$transparent_medium_white: transparentize($white, 0.4)

$transparent_brand_color: transparentize($brand_color, 0.05)